@media screen and (max-width: 991px) {
  .product{
    &-grid{
      grid-template-columns: repeat(2,minmax(0,1fr));
      grid-auto-rows: auto;
    }
  }
}

@media screen and (max-width: 768px) {
  .header{
    padding-top: 16px;
    &-wrapper{
      height: 38px;
      padding: 0 5px;
    }
    &-social{
      &__icon{
        width: 26px;
        height: 26px;
        margin-right: 8px;
      }
    }
    &__profile{
      margin-right: 8px;
    }
  }
  .inventory{
    &-wrapper{
      &__btn{
        display: none;
      }
      &__icon{
        display: block;
      }
    }
  }
  .btn{
    &-connect{
      height: 38px;
      width: 150px;
    }
  }
  .page{
    &-logo{
      padding: 66px 42px 0 42px;
    }
  }
}

@media screen and (max-width: 475px) {
  .header{
    &-wrapper{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  .profile{
    &__img{
      width: 26px;
      height: 26px;
    }
  }
  .language-change{
    &__current{
      height: 16px;
    }
    svg{
      width: 16px;
      height: 16px;
    }
  }
  .page{
    &-content{
      &__wrapper{
        padding:40px 0 80px 0;
      }
    }
    &-logo{
      &__text{
        margin-top: 55px;
      }
    }
  }
  .product{
    &-grid{
      grid-template-columns: repeat(2,minmax(0,1fr));
      grid-auto-rows: 464px;
      row-gap: 45px;
      column-gap: 4px;
    }
    &-item{
      box-shadow: 0px 0px 16px rgba(79, 126, 159, 0.7);
      border-radius: 8px;
      padding: 32px 4px;
      justify-content: flex-start;
      &__btn{
        bottom: -23px;
        width: 104px;
        margin-left: -52px;
      }
      &__img{
        width: 80px;
        height: 80px;
        img{
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
    }
  }
}