*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  font-family: 'HelveticaNeue';
  font-weight: 400;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
  height: 100%;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'Manrope';
  src: url("../fonts/Manrope-Bold/Manrope-Bold.woff2") format("woff2"), url("../fonts/Manrope-Bold/Manrope-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Manrope';
  src: url("../fonts/Manrope-Regular/Manrope-Regular.woff2") format("woff2"), url("../fonts/Manrope-Regular/Manrope-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Press Start 2P';
  src: url("../fonts/PressStart2P-Regular/PressStart2P-Regular.woff2") format("woff2"), url("../fonts/PressStart2P-Regular/PressStart2P-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Manrope', sans-serif;
  font-weight: 400;
  background: linear-gradient(180deg, #183D58 0%, #111111 22.13%);
  min-height: 100vh;
  position: relative;
}

body::before {
  content: '';
  width: 100%;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  height: 350px;
  background: url("../img/header-bg.svg") center bottom no-repeat;
  background-size: cover;
  z-index: 1;
}

.container {
  max-width: 1080px;
  width: 100%;
  margin: 0 auto;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.btn-connect {
  background: #D35626;
  border-radius: 8px;
  font-size: 14px;
  color: #fff;
  width: 180px;
  height: 40px;
}

.btn-inventory {
  background: #6DEB47;
  border-radius: 39px;
  height: 55px;
  width: 210px;
  font-size: 14px;
  color: #000;
  font-family: 'Press Start 2P', cursive;
  font-weight: 400;
}

.social-icon {
  width: 42px;
  height: 42px;
  flex-shrink: 0;
}

.social-icon svg {
  width: 100%;
  height: 100%;
}

.profile__img {
  display: block;
  width: 42px;
  height: 42px;
  border-radius: 50%;
  overflow: hidden;
}

.profile__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.language-change {
  position: relative;
}

.language-change.open .language-change__list {
  z-index: 99;
  opacity: 1;
}

.language-change.open .language-change__current::after {
  transform: rotate(180deg);
}

.language-change__list {
  position: absolute;
  top: 110%;
  left: 0;
  z-index: -1;
  opacity: 0;
  transition: .3s z-index, .3s opacity;
}

.language-change__current {
  position: relative;
  height: 20px;
}

.language-change__current::after {
  content: '';
  position: absolute;
  top: 10px;
  margin-top: -5px;
  right: -14px;
  width: 10px;
  height: 10px;
  display: block;
  background: url("../img/caret-down.svg") center center no-repeat;
  background-size: contain;
  transition: .3s linear;
}

.language-change__current:hover {
  cursor: pointer;
}

.language-change svg {
  width: 20px;
  height: 20px;
}

.header {
  padding-top: 14px;
  position: relative;
  z-index: 3;
}

.header-wrapper {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
  height: 55px;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
}

.header-wrapper__block {
  display: flex;
  align-items: center;
}

.header-wrapper__block_connect {
  justify-content: center;
}

.header-wrapper__block_inventory {
  justify-content: flex-end;
}

.header-social__icon {
  margin-right: 35px;
}

.header__profile {
  margin-right: 40px;
}

.inventory-wrapper__icon {
  width: 24px;
  height: 24px;
  display: none;
}

.inventory-wrapper__icon img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.page-wrapper {
  position: relative;
  z-index: 2;
}

.page-logo {
  padding: 30px 42px 0 42px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.page-logo__img {
  max-width: 325px;
  max-height: 160px;
}

.page-logo__img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.page-logo__text {
  font-family: 'Press Start 2P', cursive;
  color: #6DEB47;
  text-align: center;
  margin-top: 100px;
}

.page-logo__text {
  font-size: 24px;
}

@media screen and (min-width: 320px) {
  .page-logo__text {
    font-size: calc(24px + 30 * ((100vw - 320px) / 1600));
  }
}

@media screen and (min-width: 1920px) {
  .page-logo__text {
    font-size: 54px;
  }
}

.page-content__wrapper {
  padding: 90px 0 120px 0;
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 90px 20px;
  grid-auto-rows: 360px;
}

.product-item {
  background: #000000;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 30px 37px 20px 37px;
  position: relative;
}

.product-item__title {
  font-size: 25px;
  font-weight: 700;
  text-align: center;
  color: #02EF02;
}

.product-item__desc {
  font-weight: 400;
  font-size: 13px;
  line-height: 18px;
  color: #fff;
  text-align: center;
  margin-top: 6px;
}

.product-item__price {
  font-weight: 600;
  font-size: 18px;
  text-align: center;
  color: #4D7A7B;
  margin-top: auto;
}

.product-item__count {
  font-size: 13px;
  color: #ffffff;
}

.product-item__img {
  height: 80px;
  width: 80px;
}

.product-item__img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.product-item__btn {
  position: absolute;
  bottom: -29px;
  left: 50%;
  margin-left: -105px;
  width: 210px;
  height: 46px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #6EEC47;
  border-radius: 40px;
  font-weight: 700;
  font-size: 14px;
  color: #000;
  font-family: 'Press Start 2P', cursive;
}

.product-item__btn_sold {
  background: #838F97;
  border-radius: 11px;
  font-family: 'Manrope', sans-serif;
  pointer-events: none;
}

@media screen and (max-width: 991px) {
  .product-grid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-auto-rows: auto;
  }
}

@media screen and (max-width: 768px) {
  .header {
    padding-top: 16px;
  }
  .header-wrapper {
    height: 38px;
    padding: 0 5px;
  }
  .header-social__icon {
    width: 26px;
    height: 26px;
    margin-right: 8px;
  }
  .header__profile {
    margin-right: 8px;
  }
  .inventory-wrapper__btn {
    display: none;
  }
  .inventory-wrapper__icon {
    display: block;
  }
  .btn-connect {
    height: 38px;
    width: 150px;
  }
  .page-logo {
    padding: 66px 42px 0 42px;
  }
}

@media screen and (max-width: 475px) {
  .header-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .profile__img {
    width: 26px;
    height: 26px;
  }
  .language-change__current {
    height: 16px;
  }
  .language-change svg {
    width: 16px;
    height: 16px;
  }
  .page-content__wrapper {
    padding: 40px 0 80px 0;
  }
  .page-logo__text {
    margin-top: 55px;
  }
  .product-grid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-auto-rows: 464px;
    row-gap: 45px;
    column-gap: 4px;
  }
  .product-item {
    box-shadow: 0px 0px 16px rgba(79, 126, 159, 0.7);
    border-radius: 8px;
    padding: 32px 4px;
    justify-content: flex-start;
  }
  .product-item__btn {
    bottom: -23px;
    width: 104px;
    margin-left: -52px;
  }
  .product-item__img {
    width: 80px;
    height: 80px;
  }
  .product-item__img img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}
