.header {
  padding-top: 14px;
  position: relative;
  z-index: 3;

  &-wrapper {
    max-width: 1170px;
    width: 100%;
    margin: 0 auto;
    height: 55px;
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));

    &__block {
      display: flex;
      align-items: center;

      &_connect {
        justify-content: center;
      }

      &_inventory {
        justify-content: flex-end;
      }
    }
  }

  &-social {
    &__icon {
      margin-right: 35px;
    }
  }

  &__profile {
    margin-right: 40px;
  }
}

.inventory{
  &-wrapper{
    &__icon{
      width: 24px;
      height: 24px;
      display: none;
      img{
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
  }
}