.product{
  &-grid{
    display: grid;
    grid-template-columns: repeat(3,minmax(0,1fr));
    gap:90px 20px;
    grid-auto-rows: 360px;
  }
  &-item{
    background: #000000;
    border-radius: 30px;

    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 30px 37px 20px 37px;

    position: relative;
    &__title{
      font-size: 25px;
      font-weight: 700;
      text-align: center;

      color:#02EF02;
    }
    &__desc{
      font-weight: 400;
      font-size: 13px;
      line-height: 18px;
      color:#fff;
      text-align: center;

      margin-top: 6px;
    }
    &__price{
      font-weight: 600;
      font-size: 18px;
      text-align: center;
      color: #4D7A7B;
      margin-top: auto;
    }
    &__count{
      font-size: 13px;
      color:#ffffff;
    }
    &__img{
      height: 80px;
      width: 80px;
      img{
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
    &__btn{
      position: absolute;
      bottom: -29px;
      left: 50%;
      margin-left: -105px;

      width: 210px;
      height: 46px;
      display: flex;
      align-items: center;
      justify-content: center;

      background: #6EEC47;
      border-radius: 40px;

      font-weight: 700;
      font-size: 14px;
      color:#000;
      font-family: 'Press Start 2P', cursive;
      &_sold{
        background: #838F97;
        border-radius: 11px;
        font-family: 'Manrope', sans-serif;
        pointer-events: none;
      }
    }
  }
}