.page {
  &-wrapper{
    position: relative;
    z-index: 2;
  }
  &-logo {
    padding: 30px 42px 0 42px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    &__img{
      max-width: 325px;
      max-height: 160px;
      img{
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
    &__text {
      font-family: 'Press Start 2P', cursive;
      @include fluid-type($min_width, $max_width, 24px, 54px);
      color: #6DEB47;

      text-align: center;
      margin-top: 100px;
    }
  }
  &-content{
    &__wrapper{
      padding:90px 0 120px 0;
    }
  }
}