*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Manrope', sans-serif;
  font-weight: 400;
  background: linear-gradient(180deg, #183D58 0%, #111111 22.13%);
  min-height: 100vh;
  position: relative;
  &::before{
    content: '';
    width: 100%;
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    height: 350px;
    background: url("../img/header-bg.svg") center bottom no-repeat;
    background-size: cover;
    z-index: 1;
  }
}


.container{
  max-width: 1080px;
  width: 100%;
  margin: 0 auto;
}

.btn{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  &-connect{
    background: #D35626;
    border-radius: 8px;
    font-size: 14px;
    color:#fff;
    width: 180px;
    height: 40px;
  }
  &-inventory{
    background: #6DEB47;
    border-radius: 39px;
    height: 55px;
    width: 210px;

    font-size: 14px;
    color:#000;
    font-family: 'Press Start 2P', cursive;
    font-weight: 400;
  }
}

.social-icon{
  width: 42px;
  height: 42px;
  flex-shrink: 0;
  svg{
    width: 100%;
    height: 100%;
  }
}

.profile{
  &__img{
    display: block;
    width: 42px;
    height: 42px;
    border-radius: 50%;
    overflow: hidden;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.language-change{
  position: relative;
  &.open{
    .language-change{
      &__list{
        z-index: 99;
        opacity: 1;
      }
      &__current{
        &::after{
          transform: rotate(180deg);
        }
      }
    }
  }
  &__list{
    position: absolute;
    top: 110%;
    left:0;
    z-index: -1;
    opacity: 0;
    transition: .3s z-index, .3s opacity;
  }
  &__current{
    position: relative;
    height: 20px;
    &::after{
      content: '';
      position: absolute;
      top: 10px;
      margin-top: -5px;
      right: -14px;
      width: 10px;
      height: 10px;
      display: block;
      background: url("../img/caret-down.svg") center center no-repeat;
      background-size: contain;
      transition: .3s linear;
    }
    &:hover{
      cursor: pointer;
    }
  }
  svg{
    width: 20px;
    height: 20px;
  }
}